  methods: {
    scrollTo(index, e) {
      if (this.isMove) {
        return;
      }
      this.i = index;

      // 计算移动距离
      const { parent } = this.$refs;
      const pWidth = parent.offsetWidth;
      const sWidth = e.target.offsetWidth;
      const sToLeft = e.target.getBoundingClientRect().left;
      // parent.scrollLeft += sToLeft + sWidth / 2 - pWidth / 2;
      tools.moveTo(parent.scrollLeft, sToLeft + sWidth / 2 - pWidth / 2, parent, 'scrollLeft');

      // 获取侧边栏数据
      this.$store.dispatch('getSideList', this.menuList[index].title);
    },
  },
  mounted() {
    this.$store.dispatch('getSideList', this.menuList[0].title);
  },
