<template>
  <div class="shopping">
    <NavBar>
      <div slot="title" class="nav-title">购物车</div>
      <div slot="right" class="nav-title" @touchend="del">清空购物车</div>
    </NavBar>
    <div class="card">
      <van-checkbox-group v-model="result" ref="checkboxGroup">
        <div class="card-box" v-for="item in list" :key="item.id">
          <van-checkbox class="check" :name="item.id"></van-checkbox>
          <GoodsCard
            v-bind="item"
            :count="counterMap[item.id]"
          ></GoodsCard>
        </div>
      </van-checkbox-group>
    </div>
    <van-submit-bar
      :price="totalMoney"
      :button-text="`结算(${totalNum})`"
      @submit="onSubmit"
      v-if="list.length > 0"
    >
      <van-checkbox v-model="checked" @click="checkAll">全选</van-checkbox>
    </van-submit-bar>
    <div class="shoppingcart" v-else>
      <svg class="icon" width="128px" height="128.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"><path fill="#cdcdcd" d="M298.666667 938.666667a85.333333 85.333333 0 1 0 0-170.666667 85.333333 85.333333 0 0 0 0 170.666667zM725.333333 938.666667a85.333333 85.333333 0 1 0 0-170.666667 85.333333 85.333333 0 0 0 0 170.666667zM147.072 89.856a42.666667 42.666667 0 1 0-38.144 76.288l42.794667 21.418667a42.666667 42.666667 0 0 1 23.424 34.304l31.317333 344.405333A128 128 0 0 0 333.909333 682.666667h369.706667a128 128 0 0 0 123.093333-92.842667l52.096-182.4A85.333333 85.333333 0 0 0 796.757333 298.666667H267.818667l-7.68-84.522667A128 128 0 0 0 189.866667 111.232l-42.837334-21.376z"  /></svg>
      <span>购物车空空如也，快去选购商品吧！</span>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex';
import NavBar from '@/components/Navbar.vue';
import GoodsCard from '@/components/GoodsCard.vue';
import api from '@/api/index';
import { Toast, Dialog } from 'vant';

export default {
  data() {
    return {
      result: [], // 选中的值
      list: [], // 当前组件的商品
      checked: false,
    };
  },
  computed: {
    ...mapGetters(['counterMap']),
    totalNum() {
      const newResult = this.list.filter((item) => this.result.includes(item.id));
      const res = newResult.reduce((prev, next) => prev + this.counterMap[next.id] || 0, 0);
      return res;
    },
    totalMoney() {
      const newResult = this.list.filter((item) => this.result.includes(item.id));
      const res = newResult.reduce((prev, next) => {
        const count = this.counterMap[next.id] || 0;
        return Math.round(count * next.price * 100) + prev;
      }, 0);
      return res;
    },
  },
  components: {
    NavBar,
    GoodsCard,
  },
  methods: {
    ...mapMutations(['storageChange']),
    async getData() {
      const result = Object.keys(this.counterMap);
      const res = await api.getGoodsByIds(result.join());
      this.list = res.list;
    },
    checkAll() {
      if (this.checked) {
        this.$refs.checkboxGroup.toggleAll(true);
      } else {
        this.$refs.checkboxGroup.toggleAll(false);
      }
    },
    async del() {
      if (this.result.length === 0) {
        Toast('你没有选中任何商品！');
      }
      try {
        await Dialog.confirm({ message: '你确定要删除选中的商品吗？' });
        this.result.forEach((id) => {
          this.storageChange({ id, value: -Infinity });
          this.list = this.list.filter((item) => !this.result.includes(item.id));
        });
      } catch (error) {
        Toast('你点击了取消！');
      }
    },
    onSubmit() {},
  },
  created() {
    this.getData();
  },
  watch: {
    result() {
      if (this.result.length === this.list.length) {
        this.checked = true;
      } else {
        this.checked = false;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.shopping {
  .nav-title {
    font-size: 14px;
    color: #fff;
  }
  .card {
    width: 100%;
    position: absolute;
    top: 46px;
    z-index: 0;
    box-sizing: border-box;
    padding: 10px 10px 100px 10px;
    .card-box {
      display: flex;
      justify-content: center;
      .check {
        margin-right: 10px;
        flex-shrink: 0;
      }
    }
  }
  .shoppingcart {
    height: calc(100vh - 96px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    span {
      color: #333;
      font-size: 16px;
      margin-top: 10px;
    }
  }
}
.van-submit-bar {
  bottom: 50px;
  background: #eee;
}
</style>
