import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '@/views/Home.vue';
import Classify from '@/views/Classify.vue';
import Shopping from '@/views/Shopping.vue';
import Mine from '@/views/Mine.vue';
import Vegetables from '@/views/Home/Vegetables.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    redirect: 'home',
  },
  {
    path: '/home',
    component: Home,
    children: [
      {
        path: '/',
        redirect: 'vegetables',
      },
      {
        path: 'vegetables',
        name: 'Vegetables',
        component: Vegetables,
      },
      {
        path: 'fruit',
        name: 'Fruit',
        component: () => import('@/views/Home/Fruit.vue'),
      },
      {
        path: 'seafood',
        name: 'Seafood',
        component: () => import('@/views/Home/Seafood.vue'),
      },
      {
        path: 'meat',
        name: 'Meat',
        component: () => import('@/views/Home/Meat.vue'),
      },
    ],
  },
  {
    path: '/classify',
    name: 'Classify',
    component: Classify,
  },
  {
    path: '/shopping',
    name: 'Shopping',
    component: Shopping,
  },
  {
    path: '/mine',
    name: 'Mine',
    component: Mine,
  },
  {
    path: '/search',
    component: () => import('@/views/Search.vue'),
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('@/components/Login.vue'),
  },
  {
    path: '/register',
    name: 'Register',
    component: () => import('@/components/Register.vue'),
  },
];

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes,
});

export default router;
