import axios from 'axios';
import baseURL, { URLS } from './urls';

const appkey = 'Crip_1615210881991';

const instance = axios.create({
  baseURL,
  params: {
    appkey,
  },
});

instance.interceptors.response.use((resp) => resp.data);

// 获取侧边栏数据
const getSideList = (data) => instance.get(
  URLS.getsidebar, { params: { data } },
);

// 获取商品列表
const getGoodsList = (type, page, size, sort) => instance.get(
  URLS.getGoodsList,
  {
    params: {
      type, page, size, sort,
    },
  },
);

// 购物车数据
const getGoodsByIds = (value) => instance.get(
  URLS.getGoodsByIds,
  {
    params: {
      value,
    },
  },
);

// 模糊查询
const likeSearch = (likeValue) => instance.get(
  URLS.likeSearch,
  {
    params: {
      likeValue,
    },
  },
);

// 查询
const search = (type, page, size) => instance.get(
  URLS.search,
  {
    params: {
      type, page, size,
    },
  },
);

export default {
  getSideList,
  getGoodsList,
  getGoodsByIds,
  likeSearch,
  search,
};
