<template>
  <div class="goods-list">
    <div class="header van-hairline--top-bottom">
      <div :class="{active: type == 'all'}" @touchend="changeType('all')">综合</div>
      <div :class="{active: type == 'sale'}" @touchend="changeType('sale')">销量</div>
      <div class="price"
        :class="{'price-up': type == 'price-up', 'price-down': type == 'price-down'}"
        @touchend="changeType('price-up')"
      >价格</div>
    </div>
    <div class="list-content">
      <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          :immediate-check="false"
          @load="onLoad"
        >
          <GoodsCard
            v-for="(item, index) in GoodsList"
            :key="index"
            v-bind="item"
            :count="counterMap[item.id]"
          >
          </GoodsCard>
        </van-list>
      </van-pull-refresh>
    </div>
  </div>
</template>

<script>
import GoodsCard from '@/components/GoodsCard.vue';
import { mapMutations, mapGetters } from 'vuex';

export default {
  data() {
    return {
      type: 'all',
      refreshing: false,
      loading: false,
      finished: false,
      page: 1,
    };
  },
  computed: {
    ...mapGetters(['GoodsList']),
    ...mapGetters(['counterMap']),
  },
  components: {
    GoodsCard,
  },
  methods: {
    ...mapMutations(['resetGoodsList']),
    changeType(type) {
      if (type === 'all') {
        this.type = 'all';
      } else if (type === 'sale') {
        this.type = 'sale';
      } else if (this.type === 'price-up') {
        this.type = 'price-down';
      } else {
        this.type = 'price-up';
      }
      this.onRefresh();
    },
    onRefresh() {
      this.loading = true;
      this.finished = false;
      this.refreshing = true;
      this.page = 1;
      this.resetGoodsList();
      this.$store.dispatch('getGoodsList', { page: 1, sort: this.type });
      this.refreshing = false;
      this.loading = false;
      this.finished = true;
    },
    async onLoad() {
      this.page += 1;
      // 将loading设置为 true，表示处于加载状态
      this.loading = true;
      const result = await this.$store.dispatch('getGoodsList', { page: this.page, sort: this.type });
      if (result) {
        this.loading = false;
      } else {
        this.finished = true;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.goods-list{
  .header{
    width: 296px;
    position: relative;
    top: 0;
    left: 79px;
    padding: 0 8px;
    box-sizing: border-box;
    display: flex;
    justify-content: flex-end;
    > div{
      width: 50px;
      height: 25px;
      line-height: 25px;
      text-align: center;
      color: #cecece;
      position: relative;
    }
    .content{
      content: "";
      border: 4px solid transparent;
      position: absolute;
      right: 4px;
    }
    .active, .price-up, .price-down{
      color: #1f9806;
      font-weight: bold;
    }
    .price::before{
      .content();
      border-bottom-color: #aaa;
      top: 4px;
    }
    .price::after{
      .content();
      border-top-color: #aaa;
      bottom: 4px;
    }
    .price-up::before{
      border-bottom-color: #1f9806;
    }
    .price-down::after{
      border-top-color: #1f9806;
    }
  }
  .list-content{
    width: 296px;
    overflow: auto;
    position: fixed;
    top: 172px;
    right: 0;
    bottom: 50px;
  }
}
.van-pull-refresh {
  overflow: visible;
}
</style>
