<template>
  <div class="classify">
    <van-search
      shape="round"
      background="#1f9806"
      placeholder="请输入搜索关键词"
      @focus="focus"
    />
    <OneTab />
    <template v-if="show">
      <SideBar />
      <GoodsList />
    </template>
    <van-loading type="spinner" size="2rem" vertical v-else />
  </div>
</template>

<script>
import OneTab from '@/components/OneTab.vue';
import SideBar from '@/components/SideBar.vue';
import GoodsList from '@/components/GoodsList.vue';
import { mapGetters } from 'vuex';

export default {
  components: {
    OneTab,
    SideBar,
    GoodsList,
  },
  computed: {
    ...mapGetters(['show']),
  },
  methods: {
    focus() {
      this.$router.push('/search');
    },
  },
};
</script>

<style lang="less" scoped>
.nav-title {
  font-size: 14px;
  color: #fff;
}
</style>
