<template>
  <div id="app">
    <van-tabbar v-model="active" route active-color="#1f9806" inactive-color="#aaa">
      <van-tabbar-item to="/" icon="wap-home-o">首页</van-tabbar-item>
      <van-tabbar-item to="/classify" icon="apps-o">分类</van-tabbar-item>
      <van-tabbar-item to="/shopping"
        icon="shopping-cart-o"
        class="shop-cart"
        v-if="show"
      >
        购物车
      </van-tabbar-item>
      <van-tabbar-item to="/shopping"
        icon="shopping-cart-o"
        :badge="badge"
        class="shop-cart"
        v-else
      >
        购物车
      </van-tabbar-item>
      <van-tabbar-item to="/mine" icon="user-o">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  data() {
    return {
      active: 0,
    };
  },
  computed: {
    ...mapGetters(['counterMap']),
    badge() {
      const count = Object.values(this.counterMap).reduce((prev, next) => prev + next, 0);
      if (count > 99) {
        return '99+';
      }
      return count;
    },
    show() {
      const count = Object.values(this.counterMap).reduce((prev, next) => prev + next, 0);
      if (count > 0) {
        return false;
      }
      return true;
    },
  },
};
</script>
