<template>
  <div class="side-bar" ref="side">
    <div class="item"
      v-for="(item, index) in SideList"
      :key="item.title"
      :class="{active: i === index}"
      @touchend="scrollTo(index, $event)"
      @toouchmove="move = true"
      @touchstart="move = false"
    >{{ index == 0 ? "全部" : item }}</div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex';
import tools from '../util/tool';

export default {
  data() {
    return {
      i: 0,
      move: false,
    };
  },
  computed: {
    ...mapGetters(['SideList']),
  },
  methods: {
    ...mapMutations(['resetGoodsList']),
    scrollTo(index, e) {
      if (this.move) {
        return;
      }
      this.i = index;
      const { side } = this.$refs;
      const pHeight = side.offsetHeight;
      const sHeight = e.target.offsetHeight;
      const pTop = side.getBoundingClientRect().top;
      const sTop = e.target.getBoundingClientRect().top;
      // side.scrollTop += sTop + sHeight / 2 - pHeight / 2 - pTop;
      tools.moveTo(side.scrollTop, sTop + sHeight / 2 - pHeight / 2 - pTop, side, 'scrollTop');
      // 获取列表页
      this.resetGoodsList();
      this.$store.dispatch('getGoodsList', { type: this.SideList[index], page: 1, sort: 'all' });
    },
  },
  mounted() {
    this.resetGoodsList();
    this.$store.dispatch('getGoodsList', { type: this.SideList[0], page: 1, sort: 'all' });
  },
};
</script>

<style lang="less" scoped>
.side-bar{
  width: 79px;
  position: fixed;
  left: 0;
  top: 148px;
  bottom: 50px;
  overflow: auto;
  background: #f8f8f8;
  &::-webkit-scrollbar{
    display: none;
  }
  .item{
    width: 79px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    position: relative;
    &.active{
      font-weight: bold;
      color: #1f9806;
    }
  }
  .active::before{
      content: "";
      display: block;
      width: 6px;
      height: 18px;
      background: linear-gradient(#53a311, #1f9806);
      position: absolute;
      left: 0;
      top: 50%;
      margin-top: -9px;
    }
}
</style>
