<template>
  <div class="goods-card van-hairline--bottom">
    <div class="list">
      <div class="card-img" ref="img">
        <img :src="images[0]" alt="">
      </div>
      <div class="card-content">
        <div class="title">{{ title }}</div>
        <div class="desc">{{ desc }}</div>
        <div class="tags">
          <div v-for="ele in tags" :key="ele">{{ ele }}</div>
        </div>
        <div class="price_off">￥{{ price | goodsPrice }}</div>
        <div class="counter">
          <div @touchend="counter(id, -1)" v-if="count">
            <img src="../assets/rec.png" alt="">
          </div>
          <div class="strong" v-if="count">{{ count }}</div>
          <div @touchend="counter(id, 1)">
            <img src="../assets/add.png" alt="">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex';

export default {
  props: ['images', 'title', 'desc', 'tags', 'price', 'id', 'count'],
  filters: {
    goodsPrice(money) {
      return money.toFixed(2);
    },
  },
  methods: {
    ...mapMutations(['storageChange']),
    counter(id, count) {
      this.storageChange({ id, value: count });
    },
  },
};
</script>

<style lang="less" scoped>
.goods-card{
  .list{
    width: 296px;
    height: 100px;
    display: flex;
    .card-img{
      width: 90px;
      height: 90px;
      margin-right: 20px;
      img{
        width: 90px;
        height: 90px;
      }
    }
    .card-content{
      flex: 1;
      position: relative;
      .overflow-hidden{
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      > div{
        width: 170px;
      }
      .title{
        font-size: 13px;
        color: #1a1a1a;
        margin-top: 8px;
        .overflow-hidden();
      }
      .desc{
        color: #aaa;
        font-size: 11px;
        margin-top: 8px;
        .overflow-hidden();
      }
      .tags{
        display: flex;
        margin-top: 5px;
        > div{
          font-size: 10px;
          padding: 2px;
          color: #aaa;
          border: 1px solid #aaa;
          border-radius: 3px;
          margin-right: 8px;
        }
      }
      .price_off{
        font-size: 14px;
        font-weight: 600;
        color: #1f9806;
        margin-top: 8px;
      }
      .counter{
        position: absolute;
        bottom: 12px;
        right: 15px;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        > div:not(.strong){
          width: 16px;
          height: 16px;
          img{
            width: 100%;
          }
        }
        .strong{
          padding: 0 5px;
          height: 16px;
          line-height: 16px;;
        }
      }
    }
  }
}
</style>
