/* eslint-disable no-param-reassign */
function moveTo(start, end, dom, prop) {
  let step = 0;
  let speed = 4;
  let timer = null;
  clearInterval(timer);
  if (end < 0) {
    speed *= -1;
  }
  timer = setInterval(() => {
    step += speed;
    dom[prop] = start + step;
    if (Math.abs(step) > Math.abs(end)) {
      dom[prop] = start + end;
      clearInterval(timer);
    }
  }, 2);
}

export default {
  moveTo,
};
