import Vue from 'vue';
import Vuex from 'vuex';
import api from '@/api/index';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    sideList: [], // 侧边栏数据
    goodsList: [], // 商品列表数据
    size: 5,
    show: false, // 是否显示加载图标
    type: null,
    counterMap: {}, // 商品列表
    isLogin: false, // 是否登录
  },
  getters: {
    SideList: (state) => state.sideList,
    GoodsList: (state) => state.goodsList,
    show: (state) => state.show,
    counterMap: (state) => state.counterMap,
    isLogin: (state) => state.isLogin,
  },
  mutations: {
    setSideList(state, list) {
      state.sideList = list;
    },
    setGoodsList(state, list) {
      // state.goodsList = list;
      state.goodsList = [...state.goodsList, ...list];
    },
    setShow(state, bol) {
      state.show = bol;
    },
    resetGoodsList(state) {
      state.goodsList = [];
    },
    setGoodsType(state, type) {
      state.type = type;
    },
    setCounterMap(state, list) {
      state.counterMap = list;
    },
    storageChange(state, { id, value }) {
      if (state.counterMap[id]) {
        if ((value === -1 && state.counterMap[id] === 1) || value === -Infinity) {
          Vue.delete(state.counterMap, id);
        } else {
          Vue.set(state.counterMap, id, state.counterMap[id] + value);
        }
      } else {
        Vue.set(state.counterMap, id, 1);
      }
      localStorage.setItem('goods', JSON.stringify(state.counterMap));
    },
  },
  actions: {
    async getSideList(context, text) {
      context.commit('setShow', false);
      const data = await api.getSideList(text);
      context.commit('setSideList', data);
      context.commit('setShow', true);
    },
    async getGoodsList({ state, commit }, options) {
      const { page, sort } = options;
      const type = options.type || state.type;
      commit('setGoodsType', type);
      const { list, total } = await api.getGoodsList(type, page, state.size, sort);
      commit('setGoodsList', list);
      if (total > state.goodsList.length) {
        return true;
      }
      return false;
    },
  },
  modules: {
  },
});
